<template>
  <div class="pdf-view">
    <div :style="{ width: pdfWidth, margin: `0 auto` }">
      <canvas
        v-for="page in totalPage"
        :id="'pdfCanvas' + page"
        :key="page"
      ></canvas>
    </div>
  </div>
</template>

<script>
import * as PDFJS from "pdfjs-dist/legacy/build/pdf.js";
console.log(PDFJS)
PDFJS.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${PDFJS.version}/pdf.worker.js`;
import { reactive, watch, computed, toRefs, nextTick } from "vue";

export default {
  name: "PDF",
  props: {
    // PDF 
    url: {
      type: String,
      default: "",
    },
    watermark: {
      type: String,
      default: "",
    },
  },
  setup(props, context) {
    const state = reactive({
      currentPage: 1,
      totalPage: 1,
      width: 0,
      height: 0,
      pdfWidth: "",
      pdfDoc: null,
      // 
      rendering: false,
    });

    const firstPage = computed(() => state.currentPage <= 1);
    const lastPage = computed(() => state.currentPage >= state.totalPage);

    watch(
      () => props.url,
      (nVal) => {
        if (!nVal) return;
        _initPdf();
      },
      {
        immediate: true,
      }
    );

    // 
    const firstPageHandler = () => {
      if (firstPage) {
        return;
      }
      state.currentPage = 1;
      _renderQueue();
    };

    // 
    const lastPageHandler = () => {
      if (lastPage) {
        return;
      }
      state.currentPage = state.totalPage;
      _renderQueue();
    };

    // 
    const previousPage = () => {
      if (firstPage) {
        return;
      }

      state.currentPage--;
      _renderQueue();
    };

    // 
    const nextPage = () => {
      if (lastPage) {
        return;
      }

      state.currentPage++;
      _renderQueue();
    };

    const _getRatio = (ctx) => {
      let dpr = window.devicePixelRatio || 1;
      let bspr =
        ctx.webkitBackingStorePixelRatio ||
        ctx.mozBackingStorePixelRatio ||
        ctx.msBackingStorePixelRatio ||
        ctx.oBackingStorePixelRatio ||
        ctx.backingStorePixelRatio ||
        1;
      return dpr / bspr;
    };

    const _initPdf = () => {
      PDFJS.getDocument(props.url).promise.then((doc) => {
        // 
        state.pdfDoc = doc;
        // 
        state.totalPage = doc.numPages;
        // 
        nextTick(() => {
          _renderPage();
        });
      });
    };

    const _renderPage = () => {
      // 
      state.rendering = true;

      state.pdfDoc.getPage(state.currentPage).then((page) => {
        // let canvas = document.querySelector("#pdfCanvas");
        let canvas = document.getElementById(`pdfCanvas${state.currentPage}`);
        let ctx = canvas.getContext("2d");
        // 
        let ratio = _getRatio(ctx);
        // 
        //page.view  xy
        // let dialogWidth =
        //   state.$refs["pdfDialog"].$el.querySelector(".el-dialog").clientWidth -
        //   40;
        // let pageWidth = page.view[2] * ratio;
        // let scale = dialogWidth / pageWidth;
        let viewport = page.getViewport({ scale: 1.0 });
        // 
        state.width = viewport.width * ratio;
        state.height = viewport.height * ratio;
        canvas.width = state.width;
        canvas.height = state.height;

        canvas.style.width = viewport.width + "px";
        canvas.style.height = viewport.height + "px";
        state.pdfWidth = viewport.width + "px";
        // 
        ctx.setTransform(ratio, 0, 0, ratio, 0, 0);

        page
          .render({
            canvasContext: ctx,
            viewport,
          })
          .promise.then(() => {
            if (state.currentPage < state.totalPage) {
              state.rendering = false;
              _renderWatermark(); // 
              state.currentPage += 1;
              _renderPage();
            }
          });
      });
    };

    const _renderQueue = () => {
      if (state.rendering) {
        return;
      }
      _renderPage();
    };

    const _initWatermark = () => {
      let canvas = document.createElement("canvas");
      canvas.width = 200;
      canvas.height = 200;

      let ctx = canvas.getContext("2d");
      ctx.rotate((-18 * Math.PI) / 180);
      ctx.font = "14px Vedana";
      ctx.fillStyle = "rgba(200, 200, 200, .3)";
      ctx.textAlign = "left";
      ctx.textBaseline = "middle";
      ctx.fillText(props.watermark, 50, 50);

      return canvas;
    };

    const _renderWatermark = () => {
      //   let canvas = document.querySelector("#pdfCanvas");
      let canvas = document.getElementById(`pdfCanvas${state.currentPage}`);
      let ctx = canvas.getContext("2d");

      // 
      let pattern = ctx.createPattern(_initWatermark(), "repeat");
      ctx.rect(0, 0, state.width, state.height);
      ctx.fillStyle = pattern;
      ctx.fill();
    };

    return {
      ...toRefs(state),
      firstPage,
      lastPage,
    };
  },
};
</script>

<style lang="scss" scoped>
.pdf-view {
}
</style>
